import axios from 'axios';
import { authHeader } from './authservice/auth-header';

const API_URL = process.env.VUE_APP_BASE_API_URL;

class StatistikService {
    getTopAgent(){
        return axios.get(API_URL+'statistics/top-agent', { headers: authHeader() });
    }
    getTotalAgent(){
        return axios.get(API_URL+'statistics/total-agents', { headers: authHeader() });
    }
    getTotalAdmin(){
        return axios.get(API_URL+'statistics/total-admins', { headers: authHeader() });
    }
    getTotalCs(){
        return axios.get(API_URL+'statistics/total-cs', { headers: authHeader() });
    }
    getTotalCustomer(){
        return axios.get(API_URL+'statistics/total-customers', { headers: authHeader() });
    }
    getTotalCase(){
        return axios.get(API_URL+'statistics/total-case', { headers: authHeader() });
    }

    getTotalPackage(){
        return axios.get(API_URL+'statistics/total-packages', { headers: authHeader() });
    }
    getTotalRegion(){
        return axios.get(API_URL+'statistics/total-region', { headers: authHeader() });
    }
    gettotalUpload(){
        return axios.get(API_URL+'statistics/total-uploads', { headers: authHeader() });
    }
    getTopAgent(){
        return axios.get(API_URL+'statistics/top-agents', { headers: authHeader() });
    }
    getTotalReport(){
        return axios.get(API_URL+'statistics/total-reports', { headers: authHeader() });
    }
    getRecentReport(){
        return axios.get(API_URL+'statistics/recent-reports', { headers: authHeader() });
    }
    getAgentLastUpdate(){
        return axios.get(API_URL+'statistics/agents-last-update', { headers: authHeader() });
    }
    getFinishedDaily(){
        return axios.get(API_URL+'statistics/finished-daily-report', { headers: authHeader() });
    }
    getCategoryReport(){
        return axios.get(API_URL+'statistics/total-report-by-category', { headers: authHeader() });
    }
   
}

export default new StatistikService();
